html,body {
  margin: 0;
  padding: 10px;
  font-family: sans-serif;
  height: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#5e9fff), color-stop(#0099dc), color-stop(#00889c), color-stop(#007251), to(#0e5808));
  background-image: -webkit-linear-gradient(top, #5e9fff, #0099dc, #00889c, #007251, #0e5808);
  background-image: -o-linear-gradient(top, #5e9fff, #0099dc, #00889c, #007251, #0e5808);
  background-image: linear-gradient(to bottom, #5e9fff, #0099dc, #00889c, #007251, #0e5808);
}

button, input, h4 {
  opacity: .8;
  font-size: 20px;
  font-weight: 200;
  border-radius: 5px;
}


